@import url("https://fonts.googleapis.com/css2?family=Love+Ya+Like+A+Sister&family=Open+Sans&display=swap");
html,body{height:100%;background-color:#252525;font-family:'Open Sans',sans-serif}.App{background-image:url("../assets/rick-n-morty-bg.png");background-repeat:no-repeat;background-position:center bottom;color:#fff;overflow:hidden;height:100%;margin:0 auto;text-align:center;padding-bottom:15rem}h1{color:#c1f762;font-family:'Love Ya Like A Sister';font-size:7vh}.characters-list{background-position:center 50px;background-repeat:repeat;box-shadow:inset 0 0 20px #252525;background:linear-gradient(180deg,#e4d231,#32a52d 41.07%,#2196f3 96.05%);display:grid;grid-template-columns:1fr;grid-gap:5rem 2rem;padding:5rem;position:relative;margin-top:4rem}@media (min-width:601px){.characters-list{grid-template-columns:1fr 1fr}}@media (min-width:991px){.characters-list{grid-template-columns:1fr 1fr 1fr 1fr}}.character__image img{border-radius:50%;box-shadow:2px 2px 30px #252525;width:70%;height:70%;transition:1s all ease-in-out}.character__image:hover{cursor:pointer;transform:rotate(10deg)}